@media screen and (max-width: 1800px) {
  .slick-prev, .slick-next {
    top: get-vw(235px); } }
@media screen and (max-width: 1700px) {
  .slick-prev, .slick-next {
    top: get-vw(220px); } }
@media screen and (max-width: 1590px) {
  .slick-prev, .slick-next {
    top: get-vw(237px); }
  .slick-prev {
    left: get-vw(-50px); } }
@media screen and (max-width: 1420px) {
  .slick-prev, .slick-next {
    top: get-vw(215px); } }
@media screen and (max-width: 1300px) {
  .slick-prev, .slick-next {
    top: get-vw(205px); }
  .slick-prev {
    left: get-vw(-48px); } }
@media screen and (max-width: 1000px) {
  .slick-prev {
    left: get-vw(-42px); } }
@media screen and (max-width: 500px) {
  .whyYouNeedThat {
    background-image: linear-gradient(180deg,#001d99 120vw,#fafcfc 120vw);
    &__wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas: 'left''right'; }
    &__left {
      grid-area: left;
      p {
        width: 100%;
        padding-top: 7vw;
        font-size: 4.8vw; } }
    &__right {
      grid-area: right;
      span {
        font-size: 5.25vw;
        padding: 7vw 0;
        line-height: 1.7; } }
    h2 {
      font-size: 10vw;
      &:after {
        bottom: -8vw;
        width: 25vw;
        height: 3px; } }
    &__slider {
      top: 65vw;
      img {
        max-height: 9.08333vw;
        height: 40px; }
      p {
        font-size: 3vw;
        color: #9ab4c1; } }
    .partCircleSM {
      right: -1vw;
      top: .2vw;
      width: 15vw; }


    //slider
    .slick-prev, .slick-next {
      top: 48vw;
      width: 12vw;
      height: 12vw; }

    .slick-next {
      left: 80vw; }
    .slick-prev {
      left: 68vw;
      background: url(../img/svg/left-arrow.svg), #fafcfc;
      background-size: 35%;
      background-repeat: no-repeat;
      background-position: center center; } } }

