.access {
  position: relative;
  .circleMD {
    position: absolute;
    z-index: 100;
    width: get-vw(200px);
    left: get-vw(-100px);
    top: get-vw(-50px); }
  &__wrapper {
    background: $blue;
    color: #fff;
    text-align: center;
    border-radius: 15px;
    padding: get-vw(30px) 0 get-vw(75px);
    position: relative;
    overflow: hidden;
    .partCircle {
      position: absolute;
      left: 0;
      bottom: 0;
      width: get-vw(300px);
      &SM {
        position: absolute;
        right: 0;
        top: 0;
        width: 6vw; } } }
  h2 {
    font-size: get-vw(56px); }
  p {
    width: 40%;
    margin: 0 auto;
    line-height: 1.7;
    opacity: .7;
    font-size: get-vw(18px); }
  input {
    background-color: transparent;
    border: none;
    border-bottom: get-vw(2px) solid #4563E5;
    color: #fff;
    padding: get-vw(10px) 0 get-vw(10px) get-vw(12px);
    outline: none;
    font-size: get-vw(18px);
    width: 100%;
    &:focus, &:valid {
      ~ label {
        font-size: get-vw(14px);
        top: 0;
        transform: translateY(-100%); } } }
  .form {
    width: 25%;
    margin: 0 auto;
    padding: get-vw(50px) 0 get-vw(20px); } }


button {
   border: none; }
.formGroup {
  position: relative;
  margin: 0 0 get-vw(35px); }
label {
  position: absolute;
  left: get-vw(15px);
  top: 50%;
  transform: translate(0, -50%);
  font-size: get-vw(18px);
  color: rgba(255,255,255,.6);
  cursor: text;
  transition: font-size, top, transform, 200ms ease-in-out; }

