@media screen and (max-width: 500px) {
  .business {
    &__wrapper {
      display: block;
      padding: 15vw 0; }

    &__right {
      display: block; }
    h2 {
      font-size: 10vw; }
    .number {
      font-size: 10vw; }
    .title {
      font-size: 8.5vw;
      margin: 1vw 0;
      font-weight: 500; }
    .text {
      font-size: 4.5vw;
      line-height: 1.5; }
    &__item {
      margin-bottom: 15vw; }
    .partCircle {
      width: 56vw;
      &SM {
        width: 20vw; } } } }
