@media screen and (max-width: 500px) {
  .access {
    background: #001d99;
    h2 {
      font-size: 10vw; }
    p {
      width: 100%;
      font-size: 15px; }
    .form {
      width: 100%; }
    label {
      font-size: 6vw;
      top: 60%; }
    input {
      font-size: 8vw;
      padding: 5vw 0;
      &:focus ~ label {
        font-size: 4vw; }
      &:valid ~ label {
        font-size: 4vw; } }
    .btn {
      margin: 10vw 0 15vw; }
    .partCircle {
      display: none; }
    .formGroup {
      margin: 0 0 8vw; } } }
