.offer {
  position: relative;
  background:  url(../img/map.jpg) no-repeat;
  background-position: -18vw 0;
  transition: background-position 0.7s ease-in-out;
  &.active {
    background-position: -10vw 0;
    .app {
      visibility: visible;
      opacity: 1;
      left: 0; }
    .white {
      left: 22vw;
      visibility: hidden;
      opacity: 0; }
    .blue {
      left: 22vw;
      visibility: visible;
      opacity: 1; } }
  .app {
    position: absolute;
    top: get-vw(-80px);
    width: get-vw(445px);
    visibility: hidden;
    opacity: 0;
    left: -10vw;
    transition: left 0.7s ease-in-out, visibility 0.2s ease-in-out, opacity 0.2s ease-in-out; }
  &__cover {
    background-image: linear-gradient(to right, rgba(255, 255,255, 0) 0%, rgba(250, 252, 252, 1) 45%); }
  &__wrapper {
    padding: get-vw(123px) 0;
    display: grid;
    grid-template-columns: 1fr 1fr; }
  h2 {
    font-size: get-vw(56px); }
  p {
    font-size: get-vw(18px);
    color: #9ab4c1;
    line-height: 1.7; }
  &__left {
    svg {
      position: absolute;
      left: get-vw(550px);
      top: get-vw(270px);
      width: get-vw(200px);
      .inactive {
        z-index: 4; }
      .active {
        z-index: 5; } } }

  &__right {
    align-self: center; }
  .label {
    @extend %relative;
    padding-left: get-vw(25px);
    line-height: 2.5;
    cursor: pointer;
    font-size: get-vw(16px);
    color: #9ab4c1;
    &:before {
      content: "";
      position: absolute;
      width: get-vw(7px);
      height: get-vw(7px);
      background-color: #9ab4c1;
      transform: rotateZ(45deg);
      left: -2px;
      top: get-vw(6px);
      z-index: 2; } }
  .blue {
    visibility: hidden;
    opacity: 0;
    left: 14vw;
    transition: left 0.7s ease-in-out, visibility 0.2s ease-in-out, opacity 0.2s ease-in-out; }

  .white {
    left: 14vw;
    transition: left 0.7s ease-in-out; }

  input {
    width: 1px;
    opacity: 0;
    &:checked + .label::before {
      background-color: #ffbb24; }
    &:checked + .label {
      color: #21314b; } } }
