* {
  box-sizing: border-box; }

a {
  text-decoration: none;
  &:-webkit-any-link {
    text-decoration: none; } }

html {
  overflow-x: hidden; }

body {
  font-family: $roboto;
  font-weight: 400;
  position: relative;
  overflow: hidden;
  user-select: none; }

.container {
  width: get-vw(1500px);
  margin: 0 auto; }

.btn {
  font-size: get-vw(15px);
  padding: get-vw(15px);
  display: inline-block;
  text-align: center;
  width: get-vw(220px);
  border-radius: 50px;
  color: #fff;
  transition: box-shadow 0.2s ease-in-out;


  &--bl {
    background: #103199;
    box-shadow: 0px 10px 10px rgba(16,49,153,.15);
    &:hover {
      box-shadow: 0 14px 20px rgba(16,49,153,.15); } }

  &--tur {
    background: #2de2f7;
    box-shadow: 0px 10px 10px rgba(45,226,247,.15);
    &:hover {
      box-shadow: 0 14px 20px rgba(45,226,247,.15); } } }
.invisible {
  visibility: hidden;
  opacity: 0; }






