@media screen and (max-width: 500px) {
  .offer {
    background: #fff;
    img {
      display: none; }
    svg {
      display: none; }
    &__wrapper {
      display: block;
      padding: 15vw 0; }
    h2 {
      font-size: 10vw; }
    p {
      font-size: 4.8vw; }
    .label {
      font-size: 5vw;
      padding-left: 7vw;
      line-height: 1.5;
      &:before {
        width: 2.5vw;
        height: 2.5vw;
        top: 1.3vw; } }
    .checkbox__wrapp {
      margin-bottom: 5vw; } } }
