.whyYouNeedThat {
  @extend %relative;
  overflow: hidden;
  background-image: linear-gradient(to right, $blue get-vw(950px), #fafcfc get-vw(950px));
  .partCircle {
    position: absolute;
    z-index: 0;
    left: 0;
    bottom: 0;
    width: 28vw;
    &SM {
      position: absolute;
      right: 49.01042vw;
      top: 0.20833vw;
      width: 7vw; } }
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
 }    //height: get-vw(570px)
  &__left {
    color: #fff;
    padding: get-vw(100px) 0;
    h2 {
      font-size: get-vw(56px);
      @extend %relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: get-vw(-30px);
        width: get-vw(75px);
        height: 2px;
        background: #fff; } }
    p {
      width: 70%;
      padding-top: get-vw(20px);
      line-height: 1.7;
      opacity: 0.7; } }
  &__right {
    @extend %relative;
    span {
      display: inline-block;
      font-size: get-vw(24px);
      padding: get-vw(100px) 0 0 get-vw(50px); } }
  &__slider {
    position: absolute;
    left: 0;
    top: 300px;
    width: 100%;
    p {
      font-size: get-vw(14px);
      color: #9AB4C1; }
    img {
      height: get-vw(40px); } } }



//slider goes here
.slick-prev, .slick-next {
  top: get-vw(246px);
  width: get-vw(50px);
  height: get-vw(50px); }

.slick-prev {
  z-index: 5;
  left: get-vw(-52px);
  &:after {
    content: '<'; } }

.slick-next:before, .slick-prev:before {
  font-size: 0; }

.slick-next {
  left: get-vw(-2px);
  background-size: cover; }

.slick-prev, .slick-prev:focus, .slick-prev:hover {
  background: url(../img/svg/left-arrow.svg), #fff; }
.slick-next,.slick-next:focus, .slick-next:hover {
  background: url(../img/svg/right-arrow.svg), $blue; }


.slick-prev, .slick-prev:focus, .slick-prev:hover,
.slick-next,.slick-next:focus, .slick-next:hover {
  background-size: 35%;
  background-repeat: no-repeat;
  background-position: center center; }

.slider__item {
  margin-left: get-vw(50px);
  outline: none; }

