@media screen and (max-width: 500px) {
  .whatIsThat {
    &__scroll-text {
      display: none; }
    &__wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr .6fr;
      grid-template-areas: 'right' 'left'; }
    &__left {
      grid-area: left;
      svg {
        top: 10%;
        left: 25%;
        width: 45vw; } }
    &__right {
      grid-area: right; }
    h2 {
      font-size: 14vw; }
    p {
      font-size: 5vw;
      width: 100%; }
    .circleMD {
      position: absolute;
      z-index: -1;
      width: 46vw;
      right: 64vw;
      top: -1vw; }
    img {
      width: 165vw;
      right: -40vw; } } }
